<template>
  <view class="activity-container">
    <view class="activity-header" @click="handleClick(item)">
      <view class="user-wrap">
        <uv-avatar
          :customStyle="{
            marginRight: '24rpx',
            width: '80rpx',
            height: '80rpx',
            borderRadius: '50%',
            flexShrink: 0,
          }"
          :src="showAvatar"
        ></uv-avatar>
        <view class="user-info-box">
          <view class="flex items-center">
            <text class="uv-name uv-line-1">{{ showName }}</text>
            <LevelIcon :level="item.instructor_info?.level" />
          </view>
          <view class="uv-time text-sm">
            {{ item.startTime }}
          </view>
        </view>
      </view>
      <view v-if="type == 2" class="check-status" :class="getClass(item.status)">
        {{ item.status_str }}
      </view>
      <view v-else class="hot">
        <uv-icon :name="hot" width="20" height="25"></uv-icon>
        <text class="hot-num">{{ item.hot }}</text>
      </view>
    </view>
    <view class="activity-des" @click="handleClick(item)">
      <rich-text :nodes="formattedText"></rich-text>
    </view>
    <view @click="handleClick(item)">
      <uv-album
        v-if="showImageAlbum.length > 0"
        :urls="showImageAlbum"
        space="12rpx"
        :multipleSize="multipleSize"
        singleSize="400rpx"
        :previewFullImage="type === 4"
      ></uv-album>
    </view>

    <view class="activity-location">
      <uv-icon name="map" color="#A5ADBA" size="16"></uv-icon>
      <view class="text uv-line-1" @click="toLocation(item)">{{ item.address }}</view>
    </view>
    <view class="tip-box" v-if="showAreaTip">
      <view class="left">{{ tipText }}</view>
      <view class="right" @click="handleShowTip">
        <uv-icon name="info-circle" color="#A5ADBA" size="16"></uv-icon>
      </view>
    </view>
    <view class="activity-footer">
      <view class="foo-left">
        <text>{{ item.join_num }}人参加</text>
      </view>
      <view class="foo-right">
        <view class="f-right-item" @click="handlePraise">
          <uv-icon :name="item.is_love ? likeIcon : praise" size="20"></uv-icon>
          <text>{{ item.love || 0 }}</text>
        </view>
        <view class="f-right-item" @click="handleComment" v-if="showComment">
          <uv-icon :name="item.is_evaluate ? commentIcon : comment" size="20"></uv-icon>
          <text>{{ commentButtonText }}</text>
        </view>
        <view v-if="type == 2" class="f-right-item del" @click="handleDel">
          <uv-icon name="trash" color="#A5ADBA" size="23"></uv-icon>
        </view>
      </view>
    </view>

    <view>
      <uv-popup ref="addUserPopup" mode="bottom" :round="16">
        <view class="userBox">
          <view class="title">
            <text>服务评价</text>
            <view class="close" @click="closeComment">
              <uv-icon name="close"></uv-icon>
            </view>
          </view>
          <view class="card-item">
            <view class="tip">{{ commentPopupTitle }}</view>
            <view class="rate-box">
              <view class="left">
                <uv-rate
                  :value="rateValue"
                  ref="rateRef"
                  size="48rpx"
                  @change="changeRate"
                  :activeIcon="starFill"
                  :inactiveIcon="star"
                ></uv-rate>
              </view>
              <view class="right">{{ rateText }}</view>
            </view>
          </view>
          <bottomButton
            v-if="showCommentAddButton"
            @customClick="handleSubmitComment"
            type="primary"
            class="apply-btn flx-center"
          >
            提交
          </bottomButton>
        </view>
      </uv-popup>
    </view>
  </view>
</template>

<script>
import hot from '@/static/images/hot.png'
import share from '@/static/images/share.png'
import gjj from '@/static/images/gjj.png'
import yiji from '@/static/images/yiji.png'
import erji from '@/static/images/erji.png'
import praiseFill from '@/static/images/praise-fill.png'
import praise from '@/static/images/praise.png'
import commentFill from '@/static/images/comment-fill.png'
import comment from '@/static/images/comment.png'
import star from '@/static/images/star.png'
import starFill from '@/static/images/star-fill.png'
import uniStorage from '@/utils/storages/uniStorage'
import { isMp } from '@/utils/platform'
import LevelIcon from '@/components/LevelIcon/index.vue'
export default {
  name: 'ActivityItem',
  components: {
    LevelIcon,
  },
  props: {
    item: {
      type: Object,
      default: () => ({}),
    },
    type: {
      type: Number,
      default: 0, // 默认1  2 动态 3我的点赞4详情
    },
    showComment: {
      type: Boolean,
      default: false,
    },
    showAreaTip: {
      // 显示场地免费提示
      type: Boolean,
      default: false,
    },
    multipleSize: {
      type: String,
      default: '220rpx',
    },
  },
  data() {
    return {
      praise,
      praiseFill,
      comment,
      commentFill,
      share,
      hot,
      gjj,
      yiji,
      erji,
      star,
      starFill,
      rateValue: 0,
      rateDisabled: false,
    }
  },
  computed: {
    commentButtonText() {
      return this.item.is_evaluate ? '已评价' : '评价'
    },
    rateText() {
      let scoreMap = {
        1: '非常差',
        2: '差',
        3: '一般',
        4: '满意',
        5: '非常满意',
      }
      return scoreMap[Number(this.rateValue)] || ''
    },
    tipText() {
      let title = this.item.is_free == 1 ? '场地免费' : '场地收费'
      if (this.item.is_free == 2) {
        title += `:${this.item.is_free_note}`
      }
      return title
    },
    commentPopupTitle() {
      return this.item.is_evaluate ? '总体评价' : '您对该服务活动是否满意？'
    },
    showCommentAddButton() {
      return this.item.is_evaluate == 0
    },
    showName() {
      return this.item.instructor_info?.name || this.item.member_name
    },
    showAvatar() {
      return this.item.instructor_info?.avatar || ''
    },
    formattedText() {
      if (!this.item.text) {
        return ''
      }
      return this.item.text
        .split('\n')
        .map((line) => ({ name: 'p', children: [{ type: 'text', text: line }] }))
    },
    showImageAlbum() {
      return this.item.images ? this.item.images.map((e) => e.full_url) : []
    },
    likeIcon() {
      return this.$store.app.currentThemeIconByType['LIKE_ICON']
    },
    commentIcon() {
      return this.$store.app.currentThemeIconByType['COMMENT_ICON']
    },
  },

  emits: ['deleteVoluntary', 'updateVoluntary'],
  unLoad() {
    uniStorage.remove('ACTIVITYITEM')
  },
  methods: {
    returnText(item, type) {
      if (type == 4) {
        return item.text
      } else {
        const newText = `${item.text.substr(0, 100)}...`
        return newText
      }
    },
    toLocation(item) {
      if (!isMp) {
        window.location.href = `https://apis.map.qq.com/uri/v1/marker?marker=coord:${item.lat},${item.lng};title:${item.address};addr:${item.address}&referer=社体指导员移动端`
        return
      }
      uni.openLocation({
        latitude: item.lat,
        longitude: item.lng,
        name: item.address,
        address: item.address,
      })
    },
    previewImage(index) {
      let urlmap = this.item.images.map((e) => e.full_url)
      uni.previewImage({
        urls: urlmap,
        current: index,
        longPressActions: {
          success: function (data) {
            console.log(url)
          },
          fail: function (err) {
            console.log(err.errMsg)
          },
        },
      })
    },
    // 点赞
    async handlePraise() {
      if (!uniStorage.get('is_login')) {
        try {
          await this.$dialog('您还未登录,去登录?', {
            showCancelButton: true,
            confirmButtonText: '确定',
            cancelButtonText: '取消',
          })
        } catch (error) {
          console.log(error)
          return
        }
        uni.navigateTo({
          url: '/pages/login/index',
        })
        return
      }
      if (this.item.status != 2) {
        return this.$toast('资源未发布,不能点赞')
      }
      this.item.is_love = !this.item.is_love
      const cancel = this.item.is_love ? 1 : 2
      const loveNum = Number(this.item.love || 0)
      this.item.love = this.item.is_love ? loveNum + 1 : loveNum - 1
      const hotNum = Number(this.item.hot || 0)
      this.item.hot = this.item.is_love ? hotNum + 5 : hotNum - 5
      const res = await this.$api.voluntaryActivityBehavior({
        voluntary_activity_id: this.item.voluntary_activity_id,
        cancel,
      })
      if (res.code == 200) {
        this.$emit('updateVoluntary', this.item)
      } else {
        this.$toast(res.message)
      }
    },
    // 评论
    async handleComment() {
      if (!uniStorage.get('is_login')) {
        try {
          await this.$dialog('您还未登录,去登录?', {
            showCancelButton: true,
            confirmButtonText: '确定',
            cancelButtonText: '取消',
          })
        } catch (error) {
          console.log(error)
          return
        }
        uni.navigateTo({
          url: '/pages/login/index',
        })
        return
      }
      if (this.item.is_evaluate == 1) {
        this.getCommentDetail()
      }
      this.$refs['addUserPopup'].open()
      this.$nextTick(() => {
        this.$refs['rateRef'].getRateItemRect()
        this.$refs['rateRef'].getRateIconWrapRect()
      })
    },
    closeComment() {
      this.$refs['addUserPopup'].close()
    },
    changeRate(value) {
      console.log(value)
      this.rateValue = value
    },
    // 提交评论
    async handleSubmitComment() {
      if (this.rateValue == 0) {
        return this.$toast('请选择评分')
      }
      this.rateDisabled = true
      const res = await this.$api.evaluateAdd({
        voluntary_activity_id: this.item.voluntary_activity_id,
        star_level: this.rateValue,
      })
      if (res.code == 200) {
        this.$toast('评价成功')
        this.item.is_evaluate = true
        this.closeComment()
        this.getCommentDetail()
      } else {
        this.$toast(res.message)
      }
      this.rateDisabled = false
    },
    // 获取评论详情
    async getCommentDetail() {
      let res = await this.$api.evaluateGet({
        voluntary_activity_id: this.item.voluntary_activity_id,
      })
      this.rateDisabled = true
      this.rateValue = res.data.star_level || 0
    },
    getIcon(type, value) {
      return this.$dict.getDictLabel('promoteLevelList', value, { labelKey: type })
    },
    getClass(status) {
      let styleClass = ''
      switch (status) {
        case 1:
          styleClass = 'warning'
          break
        case 2:
          styleClass = 'success'
          break
        case 3:
          styleClass = 'info'
          break
        default:
          styleClass = 'warning'
          break
      }
      return styleClass
    },
    handleClick(item) {
      if (this.type == 4) {
        return
      }
      if (this.type != 3) {
        uni.navigateTo({
          url:
            '/pages-sub/activityDetail/index?voluntary_activity_id=' + item.voluntary_activity_id,
        })
      }
    },
    async handleDel() {
      uni.showModal({
        title: '提示',
        confirmText: '确定',
        content: '确定删除本条动态吗？删除后不可恢复',
        success: async (e) => {
          if (e.confirm) {
            const res = await this.$api.deleteVoluntaryActivity({
              voluntary_activity_id: this.item.voluntary_activity_id,
            })
            if (res.code == 200) {
              this.$emit('deleteVoluntary', true)
            } else {
              this.$toast(res.message)
            }
          } else if (e.cancel) {
            console.log('用户点击取消')
          }
        },
      })
    },
    handleShowTip() {
      uni.showToast({
        icon: 'none',
        title: '场地费用与平台无关，需要与指导员沟通具体金额',
      })
    },
  },
  watch: {},

  // 组件周期函数--监听组件挂载完毕
  mounted() {},
  // 组件周期函数--监听组件数据更新之前
  beforeUpdate() {},
  // 组件周期函数--监听组件数据更新之后
  updated() {},
  // 组件周期函数--监听组件激活(显示)
  activated() {},
  // 组件周期函数--监听组件停用(隐藏)
  deactivated() {},
  // 组件周期函数--监听组件销毁之前
  beforeUnmount() {},
}
</script>

